
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

export default {
name: 'classifyDetail',
data() {
return {
selected: 0, //tab
searchValue: '', //
model: { //
img: require('../../assets/icon-type2.png'),
type: 1
},
priceOrder: { //ascdesc
type: 'default',
img: require('../../assets/icon-order.png')
},
screenType: {//
active: false,
img: require('../../assets/icon-screen.png')
},
showScreenList:false,
screenChecked:[],//
screenTypeList: [{
id:1,
height:'70',
type: '',
list: [{id: '1',name: '',}, {id: '12',name: '',}, {id: '123',name: '',}, {id: '1234',name: '',}, {id: '12345',name: '',}]
},{
id:2,
height:'70',
type: '',
list: [{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',},{id: '1',name: '',}, {id: '12',name: '',}, {id: '123',name: '',}, {id: '1234',name: '',}, {id: '12345',name: '',}]
}],
goodsList: [{
name: '',
price: 2143,
collect: {
type: true,
img: require('../../assets/icon-collect-active.png')
}
}, {
name: '',
price: 2143,
collect: {
type: false,
img: require('../../assets/icon-collect.png')
}
}, {
name: '',
price: 2143,
collect: {
type: false,
img: require('../../assets/icon-collect.png')
}
}]
}
},
watch: {
screenChecked:function(value){
console.log(value)
}
},
methods: {
setFocus() {
this.$refs.search.setFocus()
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onCancel() {
console.log('on cancel')
},
changeType() {
if(this.model.type == 1) {
this.model.img = require('../../assets/icon-type1.png');
this.model.type = 2;
} else {
this.model.img = require('../../assets/icon-type2.png');
this.model.type = 1;
}
},
handleCollect(item) {
if(item.collect.type) {
item.collect.img = require('../../assets/icon-collect.png');
item.collect.type = false;
} else {
item.collect.img = require('../../assets/icon-collect-active.png');
item.collect.type = true;
}
},
resetState() {
var self = this;

function priceOrderDefault() {
self.priceOrder.type = 'default';
self.priceOrder.img = require('../../assets/icon-order.png');
self.$refs.priceOrderImg.style.transform = "rotate(0deg)";
}

function screenDefault() {
self.screenType.active = false;
self.screenType.img = require('../../assets/icon-screen.png');
}
if(this.selected == 0) {
priceOrderDefault();
screenDefault();
} else if(this.selected == 1) {
screenDefault();
} else if(this.selected == 2) {
priceOrderDefault();
}
},
handleDefault() {
this.selected = 0;
this.resetState();
},
handlePriceOrder() {
this.selected = 1;
this.resetState();
if(this.priceOrder.type == 'default') {
this.priceOrder.type = 'asc';
this.priceOrder.img = require('../../assets/icon-order-active.png');
} else if(this.priceOrder.type == 'asc') {
this.priceOrder.type = 'desc';
this.$refs.priceOrderImg.style.transform = "rotate(180deg)";
} else if(this.priceOrder.type == 'desc') {
this.priceOrder.type = 'asc';
this.$refs.priceOrderImg.style.transform = "rotate(0deg)";
}
},
handleScreen() {
this.selected = 2;
this.resetState();
if(!this.screenType.acitve) {
this.screenType.active = true;
this.showScreenList = true;
this.screenType.img = require('../../assets/icon-screen-active.png');
}
},
showAll(item){
if(item.height>70){
item.height = 70;
}else{
var num = parseInt(item.list.length/3);
num = item.list.length > num*3 ? num + 1 : num;
console.log(num)
var height = 30 + 45*num;
item.height = height;
}
},
handelReset(){//
this.screenChecked.splice(0,this.screenChecked.length);
},
confirmScreen(){//
this.showScreenList = false;
}
},
components: {
Search,
Tab,
TabItem,
Checker,
CheckerItem
}
}



