export default {
	name: 'cashFlow',
	data() {
		return {
			//underway:success:fail:
			list:[{
				name:'',
				state:'underway',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'',
				state:'underway',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'',
				state:'fail',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'',
				state:'success',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'',
				state:'success',
				time:'2017-12-20',
				count:'20.00'
			}]
		}
	},
	filters:{
		filtState:function(value){
			if(value == 'underway'){
				value = '';
			}else if(value == 'fail'){
				value = '';
			}else if(value = 'success'){
				value = '';
			}
			return value;
		}
	}
}



