
import XInput from 'vux/src/components/x-input/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'user_tel',
data() {
return {
mobile: '', //
code: '', //
getCode: {
txt: '',
disabled: false,
}
}
},
methods: {
handleGetCode() {
if(this.mobile) {
this.getCode.disabled = true;
this.getCode.txt = '...';
let seconds = 60;
let self = this;
let time = setInterval(function() {
--seconds;
self.getCode.txt = seconds + 's';
if(seconds <= 0) {
clearInterval(time);
self.getCode.disabled = false;
self.getCode.txt = '';
}
}, 1000)
} else {
this.$vux.toast.text('');
}
},
handleTel() {
if(!this.mobile || !this.code) {
this.$vux.toast.text('')
} else {
this.$vux.toast.text('')
}
this.$router.push({
path:'/my/info',
})
}
},
created() {
window.document.title = '';
},
components: {
XInput,
XButton,
Group,
Box
}
}



