
import Search from 'vux/src/components/search/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: 'home',
data() {
return {
screenList: [{
name: '',
active: true
}, {
name: '',
active: false
}, {
name: '',
active: false
}, {
name: '',
active: false
}, {
name: '',
active: false
}, {
name: '1',
active: false
}],
searchValue: '',//
screenWidth: '', //
goodsList:[{
name:'',
price:2143
},{
name:'',
price:2143
},{
name:'',
price:2143
}]
}
},
components: {
Search,
Toast
},
methods: {
setFocus() {
this.$refs.search.setFocus()
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onCancel() {
console.log('on cancel')
},
calcScreenWidth() {
var tempWidth = 0;
for(var i = 0; i < this.screenList.length; i++) {

tempWidth += this.$refs.screenBox.children[i].clientWidth + 16;
}
return tempWidth + 1;
},
changeScreen(item){
for(var i=0; i<this.screenList.length; i++){
this.screenList[i].active = false;
}
item.active = !item.active;
}
},
mounted() {
this.screenWidth = this.calcScreenWidth();
},
computed: {

}
}



