export default {
name: 'shopcar',
data() {
return {
checkedAll: false,//
shopcarList: [{//
name: 'MD100MD100MD100',
price: 12342,
count: 1,
checked:false,
}, {
name: 'MD100',
price: 12342,
count: 1,
checked:true,
}, {
name: 'MD100MD100',
price: 12342,
count: 1,
checked:true,
}],
recommendList:[{//
name:'',
price:2143
},{
name:'',
price:2143
}]
}
},
components: {
XButton,
CheckIcon,
CalcNumber
},
methods: {
change(val) {
console.log('change', val)
},
handleCheckedItem(item){
var isAll = true;
this.shopcarList.forEach((obj)=>{
if(obj.checked == false){
isAll = false;
}
})
if(isAll){
this.checkedAll = true;
}else{
this.checkedAll = false;
}
},
handleCheckedAll(){
if(this.checkedAll == true){
this.shopcarList.forEach((obj)=>{
obj.checked = true;
})
}else{
this.shopcarList.forEach((obj)=>{
obj.checked = false;
})
}
}
}
}



